import React from 'react';
import * as Icon from 'react-feather';
import { CSSTransition } from 'react-transition-group';

const NavList = ['Home', 'Projects', 'Skills', 'Resume', 'Random'];

export default function NavbarM(props) {
  const [inProp, setInProp] = React.useState(false);
  return (
    <div className='navbar-mob'>
      <div
        className='menu-mobile'
        onClick={() => {
          setInProp(!inProp);
        }}
      >
        <Icon.Menu></Icon.Menu>
        <span>xypnox</span>
      </div>
      <CSSTransition
        in={inProp}
        timeout={250}
        unmountOnExit
        classNames='navbar-m'
      >
        <div className='navbar-m'>
          {NavList.map((data, index) => {
            let active = props.page === index ? ' active' : '';
            return (
              <div
                className={'link' + active}
                onClick={() => {
                  props.setState({ page: index });
                  setInProp(false);
                }}
              >
                {data}
              </div>
            );
          })}
          <div
            className='link blog-link'
            onClick={() => {
              window.open('https://www.xypnox.com/blag/', '_blank');
            }}
          >
            Blog <Icon.ExternalLink></Icon.ExternalLink>
          </div>
        </div>
      </CSSTransition>
    </div>
  );
}
