import React, { useState } from 'react';
import Navbar from './Navbar';
import NavbarM from './NavbarM';
import Slides from './Slides';
import './App.scss';

import logo from './images/logo.svg';

const images = [logo];

function App() {
  let [state, setState] = useState({
    page: 0,
  });

  let scrollSetState = (state) => {
    window.scrollTo(0, 0);
    setState(state);
  };

  React.useEffect(() => {
    images.forEach((image) => {
      new Image().src = image;
    });
  }, []);

  console.log(state);

  return (
    <div className='App'>
      <Slides page={state.page}></Slides>

      <div className='navbar-container'>
        <Navbar setState={scrollSetState} page={state.page}></Navbar>
        <NavbarM setState={scrollSetState} page={state.page}></NavbarM>
      </div>
    </div>
  );
}

export default App;
