import React from 'react';
import PDFObject from 'pdfobject';

export default function PDFviewer(props) {
  const { containerId, pdfBlob } = props;
  React.useEffect(() => {
    PDFObject.embed(pdfBlob, `#${containerId}`, {
      pdfOpenParams: {
        zoom: 'FitW',
      },
    });
  }, [containerId, pdfBlob]);
  return <div id={containerId} />;
}
