import React from 'react';
import * as Icon from 'react-feather';

const NavList = ['Projects', 'Skills', 'Resume', 'Random'];

export default function Navbar(props) {
  return (
    <div className='navbar'>
      <div
        className={'link' + (props.page === 0 ? ' active' : '')}
        onClick={() => {
          props.setState({ page: 0 });
        }}
      >
        <Icon.Home></Icon.Home>
      </div>
      {NavList.map((data, index) => {
        let active = props.page === index + 1 ? ' active' : '';
        return (
          <div
            className={'link' + active}
            onClick={() => {
              props.setState({ page: index + 1 });
            }}
          >
            {data}
          </div>
        );
      })}
      <div
        className='link blog-link'
        onClick={() => {
          window.open('https://www.xypnox.com/blag/', '_blank');
        }}
      >
        Blog <Icon.ExternalLink></Icon.ExternalLink>
      </div>
    </div>
  );
}
