import React, { useEffect } from 'react';
import { Transition, animated } from 'react-spring/renderprops';

import Home from './pages/Home';
import Projects from './pages/Projects';
import Resume from './pages/Resume';
import Random from './pages/Random';
import Skills from './pages/Skills';

import superpowers from './images/superpowers.svg';
import logo from './images/logo.svg';
import workart from './images/work-art.svg';

const images = [logo, superpowers, workart];
const pages = [
  (style) => (
    <animated.div style={{ ...style }} key='slide-0'>
      <Home></Home>
    </animated.div>
  ),
  (style) => (
    <animated.div style={{ ...style }} key='slide-1'>
      <Projects></Projects>
    </animated.div>
  ),
  (style) => (
    <animated.div style={{ ...style }} key='slide-2'>
      <Skills></Skills>
    </animated.div>
  ),
  (style) => (
    <animated.div style={{ ...style }} key='slide-3'>
      <Resume></Resume>
    </animated.div>
  ),
  (style) => (
    <animated.div style={{ ...style }} key='slide-5'>
      <Random></Random>
    </animated.div>
  ),
];

export default function Slides(props) {
  useEffect(() => {
    images.forEach((image) => {
      new Image().src = image;
    });
  }, []);

  return (
    <div className='slides'>
      <Transition
        reset
        unique
        items={props.page}
        from={{ opacity: 0, transform: 'translate3d(500px, 100px, -500px)' }}
        enter={{ opacity: 1, transform: 'translate3d(0px, 0px, 0px)' }}
        leave={{
          opacity: 0,
          transform: 'translate3d(-500px, -100px, -500px)',
        }}
      >
        {(index) => pages[index]}
      </Transition>
    </div>
  );
}
